@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.IPMap {
  width: 100%;
  height: calc(100vh - 280px);
}

.IPMap .pin {
  width: 60px;
  height: 60px;
  background: no-repeat url(../../static/media/icon-location.7e6459d2.svg);
}

.SearchBar {
  max-width: 555px;
  width: calc(100vw - 25px - 25px);
  height: 60px;
  margin: -1px 25px 45px 25px;
}

.SearchBar input {
  border: transparent;
  border-radius: 15px 0 0 15px;
  padding: 25px;
  height: 100%;
  width: 100%;
  font-size: var(--text-input-size);
  font-family: "Rubik", sans-serif;
  background-color: white;
}
.SearchBar input:focus {
  outline: none;
}

.SearchBar button {
  border-radius: 0 15px 15px 0;
  border: 0;
  background-repeat: no-repeat;
  background-size: 10px;
  background-position: center;
  background-image: url(../../static/media/icon-arrow.2eaaa5ee.svg);
  background-color: black;
  width: 63px;
}
.SearchBar button:hover {
  cursor: pointer;
  background-color: var(--very-dark-gray);
}

.DisplayIPInfo--position {
  position: absolute;
  top: 200px;
  z-index: 1;
}

.DisplayIPInfo {
  display: flex;
  max-width: 1110px;
  width: calc(100vw - 25px - 25px);
  min-height: 160px;
  border-radius: 15px;
  padding: 35px 0;
  margin: 0 25px;
  background-color: white;
}

.DisplayIPInfo .ipInfo {
  flex: 0 0 25%;
  border-right: 1px solid var(--dark-gray);
  padding: 0 32px;
  word-break: break-word;
}
.DisplayIPInfo .ipInfo:last-child {
  border-right: 0;
}

.DisplayIPInfo .ipInfo__title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 13px;
  letter-spacing: 1px;
  color: var(--dark-gray);
}

.DisplayIPInfo .ipInfo__content {
  margin: 15px 0 0 0;
  font-weight: 500;
  font-size: 25px;
  letter-spacing: 0.02rem;
}

@media (max-width: 880px) {
  .DisplayIPInfo {
    flex-direction: column;
    min-width: unset;
    max-width: 555px;
    top: 170px;
    text-align: center;
    padding: 25px 30px 5px 30px;
  }
  .DisplayIPInfo .ipInfo {
    margin-bottom: 20px;
    border: 0;
  }
  .DisplayIPInfo .ipInfo__title {
    font-size: 10px;
  }
  .DisplayIPInfo .ipInfo__content {
    font-size: 20px;
    margin: 5px;
  }
}

.Layout {
  align-items: center;
  position: relative;
  height: 100vh;
}

.Layout .header {
  align-items: center;
  width: 100%;
  height: 280px;
  background: no-repeat url(../../static/media/pattern-bg.a60a3c85.png);
  background-size: cover;
}
.Layout .header span {
  margin: 30px;
  font-weight: 700;
  font-size: 1.9rem;
  color: white;
}

@media (max-width: 880px) {
  .Layout .header span {
    font-size: 1.5rem;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --very-dark-gray: hsl(0, 0%, 17%);
  --dark-gray: hsl(0, 0%, 59%);
  --text-input-size: 18px;
}

.App {
  font-family: "Rubik", sans-serif;
}

.flex-by-column {
  display: flex;
  flex-direction: column;
}

.flex-by-row {
  display: flex;
  flex-direction: row;
}

